import React, {Component} from 'react'

class Artpiece extends Component {
    render(){
        const {artpiece} = this.props;
        const year = new Date(artpiece.release_date);
        const imgPath = artpiece.poster_path;

        return(
            <div className="">
                
                <img src={`https://image.tmdb.org/t/p/w780/${imgPath}`} alt="" height="300" width="400" />

                <div className="film-summary">
                    <h1>{artpiece.title}</h1>
                    <p>{artpiece.release_date}</p>
                </div>
                
                
            </div>
        )
    }
}

export {Artpiece}