import React, { useState} from 'react'
import firebase from "./firebase"


    

function ShareForm() {


    const allInputs = {imgUrl: ''}
    const [imageAsFile, setInputFile] = useState('')
    const [imageAsUrl, setImageAsUrl] = useState(allInputs)


    console.log(imageAsFile)

    const handleInputFile = (e) => {
         const image = e.target.files[0]
         setInputFile(imageFile => (image))
     }
   
     const handleFireBaseUpload = e => {
       e.preventDefault()
     console.log('start of upload')
     // async magic goes here...
   
     if(imageAsFile === '' ) {
       console.error(`not an image, the image file is a ${typeof(imageAsFile)}`)
     }
     
     const uploadTask = firebase.storage.ref(`/images/${imageAsFile.name}`).put(imageAsFile)
     
     uploadTask.on('state_changed', 
     (snapShot) => {
       //takes a snap shot of the process as it is happening
       console.log(snapShot)
     }, (err) => {
       //catches the errors
       console.log(err)
     }, () => {

       // gets the functions from storage refences the image storage in firebase by the children
       // gets the download url then sets the image from firebase as the value for the imgUrl key:
       firebase.storage.ref('images').child(imageAsFile.name).getDownloadURL()
        .then(fireBaseUrl => {
          setImageAsUrl(prevObject => ({...prevObject, imgUrl: fireBaseUrl}))
        })
     })
   
     
     }

    
    
        return(
            <div>
                 <form >
                <input 
                    type = "file" 
                    onChange = {handleInputFile}
                    
                />

                <button>upload2firebase</button>
            </form>
            <img src={imageAsUrl.imgUrl} alt="image tag" />
            </div>
           
        );


}

export default ShareForm;